import React, { useEffect, useState } from "react";
import { usePosition } from 'use-position';
import { connect } from "react-redux";
import { reverseGeocode, setCities, setSearchField, fetchWeather } from "../actions";
import './App.css';
import Nav from "../components/Nav/Nav";
import WeatherCard from "../components/WeatherCard/WeatherCard";
import dotenv from "dotenv";

dotenv.config();

const mapStateToProps = (state) => {
  return { 
    searchField: state.searchCities.searchField,
    cities: state.fetchCityNames.cities,
    isPending_cn: state.fetchCityNames.isPending_cn,
    error_cn: state.fetchCityNames.error_cn,
    RGCity: state.reverseGeocode.RGCity,
    isPending_rg: state.reverseGeocode.isPending_rg,
    error_rg: state.reverseGeocode.error_rg,
    currentWeather: state.fetchWeather.currentWeather,
    dailyWeather: state.fetchWeather.dailyWeather,
    hourlyWeather: state.fetchWeather.hourlyWeather,
    isPending_wt: state.fetchWeather.isPending_wt,
    error_wt: state.fetchWeather.error_wt
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    handleChange: (event) => dispatch(setSearchField(event.target.value)),
    onFetchCityNames: () => dispatch(setCities()),
    reverseGeocode: (lat, lon) => dispatch(reverseGeocode(lat, lon)),
    fetchWeather: (lat, lon) => dispatch(fetchWeather(lat,lon))
  }
} 

const App = (props) => {
  
  const { searchField, handleChange, onFetchCityNames, cities, reverseGeocode, RGCity, fetchWeather, currentWeather, dailyWeather, hourlyWeather, isPending_wt } = props;

  const { latitude, longitude, error } = usePosition();
  const [ fetchedPosition, setFetchedPosition ] = useState(false);
  const [ position, setPosition ] = useState({lat: "", lon: ""});
  const [ units, setUnits ] = useState("standard");

  const handleClick = (e) => {
    const lat = e.target.dataset.lat;
    const lon = e.target.dataset.lon;
    setPosition({lat, lon})
  }

  const unitsChange = (e) => {
    const units = e.target.value.toLowerCase();
    setUnits(units)
  }
  
  if(RGCity){
    document.title = RGCity + " - " + new Date().toLocaleString("en-US", {timeZone: currentWeather.timeZone, day: "numeric", month: "2-digit", year: "2-digit", hour: "numeric", minute: "numeric", hourCycle: "h12"})
  }

  useEffect(() => {
    if(latitude && longitude && !fetchedPosition) {
        setPosition({ lat:latitude,  lon:longitude })
        setFetchedPosition(true)
    }

    if(position.lat && position.lon){
      fetchWeather({lat: position.lat, lon: position.lon, units, api: process.env.API})
      reverseGeocode(position.lat, position.lon, process.env.API)
    }

    console.log(process.env);

    onFetchCityNames()
  },[latitude, longitude, position.lat, position.lon, units])
  
  const filteredCity = cities.filter(city => {
    return city.cityName.toLowerCase().includes(searchField.toLowerCase())
  })

  const limitedFilterCity = filteredCity.filter((item, index) => {
    return index < 6
  })

  if(currentWeather.weather !== undefined){
    return (
      <div className={`App g${currentWeather.time < 10 ? currentWeather.time[1] : currentWeather.time}`}>
        <Nav 
        handleChange={handleChange} 
        searchField={searchField} 
        limitedFilterCity={limitedFilterCity} 
        handleClick={handleClick}
        unitsChange={unitsChange} />
        <WeatherCard 
        isPending_wt={isPending_wt}
        currentWeather={currentWeather}
        dailyWeather={dailyWeather}
        hourlyWeather={hourlyWeather}
        RGCity={RGCity}
        units={units}
        />
      </div>
    )  
  } else {
    return (
      <div>Loading</div>
    )
  }

    
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
